import { ThreeCore } from './ThreeCore';
import { Core } from './Core/Core';

export class AppThreeTemplate {
    threeCore: ThreeCore;
    core: Core;
    constructor(id: string = 'main') {
        this.threeCore = new ThreeCore(id);
        this.core = new Core(this.threeCore);
        this.BindEvent();
        this.BindRenderer();
    }
    Dummy() {
        //
    }
    BindEvent() {
        this.threeCore.MouseDown = (e: MouseEvent) => this.core.MouseDown(e);
        this.threeCore.MouseMove = (e: MouseEvent) => this.core.MouseMove(e);
        this.threeCore.MouseUp = (e: MouseEvent) => this.core.MouseUp(e);
    }
    BindRenderer() {
        this.threeCore.Render = () => this.core.Render();
    }
}


export class Solution {
    public divHost: HTMLElement;

    constructor(id: string, index = 0) {
        this.divHost = document.getElementById(id);

        const div = document.createElement('div');
        div.id ='three-starter-second';
        div.style.width = '100%';
        div.style.height = '100%';
        this.divHost.appendChild(div);

        new AppThreeTemplate('three-starter-second');
    }
    public destroy() {
        try {
            while (this.divHost !== undefined && this.divHost.lastElementChild !== undefined) {
                this.divHost.removeChild(this.divHost.lastElementChild);
            }
        } catch (error) { console.debug(error) }
    }
}