import { math } from 'polished';
import { ThreeCore } from '../ThreeCore';
import * as THREE from 'three';
export class Core {
    threeCore: ThreeCore;
    boxMeshes: THREE.Mesh[] = [];
    constructor(threeCore: ThreeCore) {
        this.threeCore = threeCore;
        for (let i = 0; i < 100; ++i) {
            const boxGeo = new THREE.BoxGeometry(10, 10, 10);


            const material = new THREE.MeshBasicMaterial( {color: new THREE.Color(Math.random() , Math.random(), Math.random())} ); 
            const boxMesh = new THREE.Mesh(boxGeo, material);
            boxMesh.position.x = 50 - (Math.random() * 100); // new THREE.Vector3(50 - (Math.random() * 100), 50 - (Math.random() * 100), 50 - (Math.random() * 100));
            boxMesh.position.y = 50 - (Math.random() * 100);
            boxMesh.position.z = 50 - (Math.random() * 100);
            boxMesh.castShadow = true;
            boxMesh['value'] = Math.random();
            this.threeCore.scene.add(boxMesh);
            this.boxMeshes.push(boxMesh);
        }
        // console.log(this.boxMeshes)
    }
    Render() {
        // console.log('ren')
        for (let i = 0; i < this.boxMeshes.length; ++i) {
            this.boxMeshes[i].rotateX(this.boxMeshes[i]['value'] * 0.01);
            this.boxMeshes[i].rotateY(this.boxMeshes[i]['value'] * 0.05);
            this.boxMeshes[i].rotateZ(this.boxMeshes[i]['value'] * 0.09);
        }
    }
    MouseDown(e: MouseEvent) {
        // console.log(e, 'core');
    }
    MouseUp(e: MouseEvent) {
        // console.log(e, 'core');
    }
    MouseMove(e: MouseEvent) {
        // console.log(e, 'core');
    }
    MouseDrag(e: MouseEvent) {
        // console.log(e, 'core');
    }
}